<div class="input-component"><input :class="['input', { input_error: error }]" v-if="type !== 'file' &amp;&amp; type !== 'radio'" v-bind="$attrs" :type="type" :value="modelValue" @input="$emit('update:modelValue', $event.target.value)"><input class="input" v-else-if="type === 'file'" v-bind="$attrs" :type="type" @change="$emit('handleChange', $event.target.files[0])"><span class="error__message" v-if="error">{{ error }}</span></div>